import React from 'react'
import {useState} from 'react'
import {graphql} from 'gatsby'
import Container from '../components/container'
import SearchBar from '../components/searchBar'

const filterPosts = require('../utils/filter')


const ArticlesSearch = ({data}) => {
  const isBrowser = typeof window !== "undefined"
  const articles = data.allDatoCmsArticle.edges
  const {search} = window.location ? window.location : ''
  const query = new URLSearchParams(search).get('s')
  const [searchQuery, setSearchQuery] = useState(query || '')
  const filteredArticles = filterPosts(articles, searchQuery)


  return (
    <Container>
      <SearchBar path="blog" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />
    {filteredArticles.map(post => {
      return (
        <li key={post.node.title}>{post.node.title}</li>
      )
    })}
    </Container>
  )
}

export const blogquery = graphql`
  query getAllArticles {
    allDatoCmsArticle {
      edges {
        node {
          title
          slug
        }
      }
    }
}
`;

export default ArticlesSearch
